const vueServerRenderer = require("vue-server-renderer");

module.exports.default = {
  async render(props) {
    const state = await require('/private/tmp/ragu-vue-test-app/ragu-components/hello-world/state').default.propsToState(props);
    const app = await require('/private/tmp/ragu-vue-test-app/ragu-components/hello-world').default.render(props, state);
    const renderer = vueServerRenderer.createRenderer();

    return {
      state: {},
      html: await renderer.renderToString(app)
    }
  }
}    
